import { cva } from 'class-variance-authority';
import { clsx } from 'clsx';
import { GlobalVariants } from '../../interfaces/global-variants';
import { HeaderShortProps } from './headershort.interface';

export const headerShortHeadlineVariants = {
  [GlobalVariants.Zps]: ['zep-text-typography-light-100'],
  [GlobalVariants.Cat]: ['zep-text-typography-light-100'],
  default: 'zep-text-indigo-500',
};

export const headerShortTaglineVariants = {
  [GlobalVariants.Zps]: ['zep-text-typography-brand-steel'],
  [GlobalVariants.Cat]: ['zep-text-typography-light-70 zep-opacity-70'],
  default: 'zep-text-typography-brand-steel',
};

export const headlineClass = cva([`zep-typography-headlineLG-fluid-cqi zep-block zep-break-words`], {
  variants: {
    variant: headerShortHeadlineVariants,
  },
  defaultVariants: {
    variant: GlobalVariants.Zps,
  },
});

export const taglineClass = cva([`zep-mb-0.5 zep-typography-taglineMD zep-block`], {
  variants: {
    variant: headerShortTaglineVariants,
  },
  defaultVariants: {
    variant: GlobalVariants.Zps,
  },
});

export const HeaderShort = ({ variant, tagline, headline, showArrow, className }: HeaderShortProps) => {
  return (
    <div className={className} data-testid="header-short">
      <div className="zep-grid zep-grid-cols-4 sm:zep-grid-cols-8 md:zep-grid-cols-16 zep-px-1 sm:zep-px-1.5 zep-gap-1.5">
        {showArrow ? (
          <img
            className={clsx(
              'zep-h-4',
              'sm:zep-h-5',
              'md:zep-h-4',
              'lg:zep-h-6',
              'zep-w-auto',
              'zep-self-center',
              'zep-col-start-1',
              'zep-col-end-2',
              'md:zep-col-start-2',
              'md:zep-col-end-3',
            )}
            alt="forward arrow"
            src="./assets/moving-forward-arrow.svg"
          ></img>
        ) : null}

        <div className="zep-block zep-self-center zep-col-start-2 zep-col-end-5 sm:zep-col-start-2 sm:zep-col-end-7 md:zep-col-start-3 md:zep-col-end-15">
          {tagline && <h3 className={taglineClass({ variant })}>{tagline}</h3>}
          <h2 className={headlineClass({ variant })}>{headline}</h2>
        </div>
      </div>
    </div>
  );
};
